
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import http from "@/http";

export default {
components: {
Scroller,
Spinner
},
methods: {
/*加载列表*/
loadData: function (broadcastId, cb) {
let _this = this;
let postData = {broadcastId:broadcastId};
http('property-api/broadcast/getBroadcast', postData, {includeApi:true})
.then(function(suc){
_this.broadCast = suc;
if(cb && typeof cb == 'function'){
cb();
}
}, function(err){
console.log({err:err});
});
},
/* 下拉刷新 */
onReachBottom:function(){
let _this = this;
let broadcastId = this.broadCast.broadcastId;
if(broadcastId){
this.loadData(broadcastId, function (){
_this.$nextTick(() => {

_this.status.pulldownStatus = 'default';
})
});
}
}
},

mounted:function(){
let {params} = this.$route;
if(params && params.id){
this.loadData(params.id)
}
},
data () {
return {
pulldownConfig:{
content: '下拉刷新',
downContent: '下拉刷新',
upContent: '加载中...',
loadingContent: '加载完成',
clsPrefix: 'xs-plugin-pulldown-'
},
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},

broadCast:{}
}
}
}



